import * as qnodeOps from './qiyi_webview/qnode-ops';
import * as comOps from './qiyi_webview/component-ops';
import * as nativeOps from './qiyi_webview/component_native-ops';

import {sendCmdToNative} from './qiyi_webview/event';

document.onload = ()=>{
    console.log('document onload')
}
//bridge 相关
window && (window.__bridge__ = {});
__bridge__.on_recv_patch_command = function(patch) {
    //window.webkit && window.webkit.messageHandlers.warn.postMessage(patch);
    directJsonToDom(patch);
}

window.webkit && window.webkit.messageHandlers.finish_construct.postMessage(0);

//dom相关
function directJsonToDom(directs){
    document.getElementById('page').style.backgroundColor = '#fff';

    sendCmdToNative("console.log('js webview get json time : " + new Date().getTime() + "')")


    Array.isArray( directs.direct_dom ) && directs.direct_dom.forEach(( value , index ) => {
        //console.log(value.op)
        qnodeOps[value.op].call( this , value.val );
    })
    Array.isArray( directs.direct_attr ) && directs.direct_attr.forEach(( value , index ) => {
        //console.log(value.op)
        qnodeOps[value.op].call( this , value.val );
    })
    Array.isArray( directs.direct_com ) && directs.direct_com.forEach(( value , index ) => {
        //console.log(value.op)
        comOps[value.op].call( this , value.val );
    })
    Array.isArray( directs.direct_native ) && directs.direct_native.forEach(( value , index ) => {
        nativeOps[value.op].call( this , value.val );
        
    })

    sendCmdToNative("console.log('js webview finish render time : " + new Date().getTime() + "')")
}




