import bridge from './bridge_view';
import { getElm } from './htmlElm';

function createNativeBox(id,el,type,viewData) {
    //create a native box on a element that has
    let data = {};
    data.top = el.offsetTop;
    data.left = el.offsetLeft;
    data.height = el.offsetHeight;
    data.width = el.offsetWidth;
    data.type = type;
    data.viewData = viewData;
    data.id = id;
    data.action = "create";
    bridge.triggerEvent("nativeBox",data);
}

function deleteNativeBoxByID(id){
    data = {};
    data.id = id;
    data.action = "delete";
    bridge.triggerEvent("nativeBox",data);
}

export function createNative( qnode ){
    createNativeBox( qnode._uid , getElm(qnode) , qnode.nativeTag , { test : 'test' } );
}

export function removeNative( qnode ){
    deleteNativeBoxByID( qnode._uid );
}



