export function sendEvent(qnode,event,params){
    let s = `QiyiBridge.bridgeGetEvent({_uid:${qnode._uid},event:'${event}'${ typeof params !== 'undefined' ? ',params:' + JSON.stringify(params) : ''}})`
    return sendCmdToNative(s)
}
export function sendCmdToNative(s){
    try{
        window.webkit && window.webkit.messageHandlers.emit.postMessage(s);
    }catch(e){

    }
    try{
        window.bridge && window.bridge.executeJS(s);
    }catch(e){

    }
    try{
        eval(s)
    }catch(e){

    }
}




