export function swiperCreate(){
    const swiperObj = new Swiper({
        direction: 'horizontal'
    });
    //swiper.on(
    //'swiped',( prev , current )=>{
    //sendEventCom({_uid : swiper.$container.id},'swiper_end',current)
    //}
    //)
    swiperObj.$container.addEventListener('touchend',()=>{
        sendEvent({_uid : swiperObj.$container.id},'swiper_end',swiperObj._current)
    })
}
export function swiperDestory(){
    addComponentDirect( 'swiperDestory' )
}

export function swiper(){
}

export function scrollEnd(id){
    initScroll(id);
    initScrollListener();
    console.log(document.getElementById(id));
}

export function scrollEndDestroy(id) {

}

let scrollObjectMap = {};

function initScroll(id) {
    let dom = document.getElementById(id);
    if(!hasScrollbar(dom)) {
        dom = document.body;
    }
    scrollObjectMap[id] = {
        uid: id,
        el: dom,
        lastScrollTop: 0
    };
}

let hasInitScroll = false;
let scrollTimer = -1;

function initScrollListener() {
    if(hasInitScroll) return true;
    hasInitScroll = true;
    window.addEventListener("scroll", function() {
        if(scrollTimer < 0) {
            scrollTimer = setTimeout(function() {
                for(let i in scrollObjectMap) {
                    checkIsScrollEnd(scrollObjectMap[i]);
                }
                clearTimeout(scrollTimer);
                scrollTimer = -1;
            }, 200);
        }
    });
}

function hasScrollbar(dom) {
    return dom.scrollHeight !== dom.clientHeight;
}

function checkIsScrollEnd(node) {
    if(node.el.scrollTop > node.lastScrollTop) {
        if(node.el.scrollHeight - node.el.scrollTop - window.screen.height < 50) {
            console.log('%c scroll end!!' + node.uid, 'background:#aaa;color:#bada55');
            sendEvent({_uid: node.uid}, 'scroll_end')
        }
    }
    node.lastScrollTop = Math.max(node.el.scrollTop, node.lastScrollTop);
}




